@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap);@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300&display=swap);@import url(https://fonts.googleapis.com/css2?family=Langar&display=swap);
body{
  margin: 0 0 0 0;
  padding: 0;
}
.header{
  padding: 0 5% 0 5%;
  background-color: #088d70;
  height: 10vh;

  color:#EBF5FB;

  display: -webkit-box;

  display: -ms-flexbox;

  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  
  font-family: 'Dancing Script', cursive;
}
.header h2{
  width: 20%;
}
.nav{
  width: 40%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.nav button{
  background: #088d70;
  color: #EBF5FB;
  border: 1px solid #EBF5FB;
  border-radius: 3px;
  padding: 10px 30px;
}
.nav button:hover {
  background:  #EBF5FB;
  color:#088d70;
}
.main{
  width: 100%;
  height: 80vh;
}


